<template>
  <div>
    <div class="edit-container">
      <el-row>
        <el-col :span="6">
          <baby-info-area :baby-id="$route.query.id" :page-type="'recipes'" />
        </el-col>
        <el-col :span="18">
          <div class="edit-content">
            <el-row class="edit-section">
              <el-col class="edit-form" :span="24">
                <div style="display: flex">
                  <div class="user-info" style="margin-right: 50px">
                    <div class="user-info-label">
                      <el-avatar
                        :size="'small'"
                        :src="ruleForm.user && ruleForm.user.avatar"
                      />
                      <div>&nbsp;&nbsp;:</div>
                    </div>
                    <div>
                      {{
                        ruleForm.user &&
                          (ruleForm.user.remark_name
                            ? ruleForm.user.remark_name
                            : ruleForm.user.nickname)
                      }}&nbsp;&nbsp;
                      <svg-icon
                        v-if="ruleForm.is_replace"
                        icon-class="replace"
                      />
                    </div>
                  </div>
                  <div class="user-info">
                    <div class="user-info-label">
                      <div>&nbsp;&nbsp;:</div>
                    </div>
                    <div>
                      {{
                        ruleForm.family_member && ruleForm.family_member.name
                      }}
                    </div>
                  </div>
                </div>
                <el-form ref="ruleForm" :model="ruleForm" label-width="130px">
                  <el-form-item
                    label=""
                    :rules="[
                      {
                        required: true,
                        message: '',
                        trigger: 'change'
                      }
                    ]"
                  >
                    <el-select
                      v-model="recipeTemplateId"
                      style="width: 65%"
                      @change="getTemplateDetail"
                    >
                      <el-option
                        v-for="item in templateDic"
                        :key="item._id"
                        :label="item.title"
                        :value="item._id"
                      />
                    </el-select>
                  </el-form-item>
                  <el-form-item
                    label=""
                    prop="title"
                    :rules="[
                      { required: true, message: '', trigger: 'blur' }
                    ]"
                  >
                    <div style="width: 65%">
                      <el-input
                        v-model="ruleForm.title"
                        maxlength="50"
                        show-word-limit
                      />
                    </div>
                  </el-form-item>
                  <el-form-item label="" prop="desc">
                    <el-input
                      v-model="ruleForm.desc"
                      :autosize="{ minRows: 5, maxRows: 100 }"
                      type="textarea"
                      maxlength="100"
                      show-word-limit
                    />
                  </el-form-item>
                  <el-form-item
                    label=""
                    :rules="[
                      { required: true, message: '', trigger: 'blur' }
                    ]"
                  >
                    <c-hot-table
                      v-model="tableContent"
                      :height="300"
                      :readonly="false"
                    />
                  </el-form-item>
                  <el-form-item
                    label=""
                    :rules="[
                      { required: true, message: '', trigger: 'blur' }
                    ]"
                  >
                    <el-input
                      v-model="description"
                      :autosize="{ minRows: 5, maxRows: 100 }"
                      type="textarea"
                      maxlength="1000"
                      show-word-limit
                    />
                  </el-form-item>
                  <el-form-item>
                    <div class="button-container">
                      <el-button @click="handleCloseTask"></el-button>
                      <el-button @click="saveTask"></el-button>
                      <el-button
                        type="primary"
                        :loading="sendLoading"
                        @click="sendTask"
                        ></el-button
                      >
                    </div>
                  </el-form-item>
                </el-form>
              </el-col>
            </el-row>
          </div>
          <div class="recipes-image">
            <HtmlToImage ref="htmlToImage" class="recipe-preview">
              <div
                class="recipe"
                :style="{
                  backgroundImage: `url(${
                    curRecipeBgOpt() ? curRecipeBgOpt().recipeBg : ''
                  })`,
                  paddingTop: '20px'
                }"
              >
                <img
                  class="recipe__header-img"
                  width="100%"
                  :src="curRecipeBgOpt() ? curRecipeBgOpt().recipeHeadBg : ''"
                  alt=""
                />
                <div class="recipe__body">
                  <div class="recipe__body__header">
                    <span class="recipe__body__header__title">{{
                      ruleForm.title
                    }}</span>
                    <span class="recipe__body__header__sign"
                      >{{ dietician }}</span
                    >
                  </div>
                  <c-table :data="effHtData" />
                  <div class="recipe__body__notes">
                    <div v-html="description" />
                  </div>
                </div>
              </div>
            </HtmlToImage>
          </div>
        </el-col>
      </el-row>
    </div>
    <task-close-dialog
      @closeTask="closeTask"
      @closeDialog="closeDialogVisible = false"
      :closeDialogVisible="closeDialogVisible"
    ></task-close-dialog>
  </div>
</template>

<script>
import BabyInfoArea from '@/views/medical-service/diagnose/extend'
import CHotTable from '@/components/ChotTable/CHotTable'
import CTable from '@/components/Ctable/CTable'
import HtmlToImage from '@/components/html2image/HtmlToImage'
import { getEff2dArrayByCols } from '@/libs/arrayUtils'
import { mapGetters, mapActions } from 'vuex'
import { recipeBgOpts } from '@/utils/recipeBgOpts.js'
import {
  getRecipesDetail,
  getTaskContent,
  upsetTask,
  closeTask
} from '@/api/recipes'
import { localRead } from '@/utils'
import { upload } from '@/api/upload'
import TaskCloseDialog from '@/components/TaskCloseDialog'

export default {
  name: 'Send',
  components: {
    BabyInfoArea,
    CHotTable,
    CTable,
    HtmlToImage,
    TaskCloseDialog
  },
  data() {
    return {
      closeDialogVisible: false,
      templateId: '',
      babyId: '',
      sendLoading: false,
      tableContent: [],
      bgType: 'A',
      description: '',
      recipeTemplateId: '',
      curRecBgOpts: recipeBgOpts,
      dietician: this.$store.state.user.name,
      effHtData: [],
      ruleForm: {},
      isSaved: false,
      createTaskId: '',
      dialogVisible: false,
      currentTask: {}
    }
  },
  computed: {
    ...mapGetters(['selectBaby', 'templateDic'])
  },
  watch: {
    tableContent: {
      handler: function(val) {
        this.effHtData = getEff2dArrayByCols(val)
      },
      deep: true
    }
  },
  mounted() {
    this.currentTask = JSON.parse(localRead('currentTask'))
    // 
    this.getTemplateDic({ show_replace: this.currentTask.is_replace ? 1 : 0 })
    this.getTaskContentFun()
  },
  methods: {
    ...mapActions({
      getTemplateDic: 'recipes/getTemplateDic'
    }),
    handleCloseTask() {
      this.closeDialogVisible = true
    },
    closeTask(reasonForm) {
      // ,
      const params = {
        reason: reasonForm.closeReasonType,
        reason_message: reasonForm.closeReason
      }
      this.closeDialogVisible = false
      closeTask(this.currentTask._id, params).then(res => {
        if (res.code === 20000) {
          this.$message.success('')
          setTimeout(() => {
            this.$router.push({
              path: 'close',
              query: {
                babyId: this.babyId,
                taskId: this.currentTask._id
              }
            })
          }, 2000)
        }
      })
    },
    getTaskContentFun() {
      // 
      getTaskContent(this.currentTask._id).then(res => {
        if (res.code === 20000) {
          const data = res.data
          this.templateId = data.recipe_template._id
          this.babyId = data.family_member._id
          if (this.currentTask.status === 0) {
            this.tableContent = data.recipe_template.content
            this.bgType = data.recipe_template.bg_type
            this.recipeTemplateId = data.recipe_template.title
            this.description = data.recipe_template.description
            this.ruleForm = data
          } else {
            this.tableContent = data.content
            this.bgType = data.recipe_template.bg_type
            this.recipeTemplateId = data.recipe_template.title
            this.description = data.description
            this.ruleForm = data
          }
        }
      })
    },
    // 
    getTemplateDetail(val) {
      this.templateId = val
      // 
      getRecipesDetail(val).then(res => {
        // ,
        this.bgType = res.data.bg_type
        this.tableContent = res.data.content
        this.recipeTemplateId = res.data._id
        this.description = res.data.description
      })
    },
    curRecipeBgOpt() {
      const recipeBgFilterOpts = this.curRecBgOpts.filter(
        opt => opt.type === this.bgType
      )
      return recipeBgFilterOpts.length === 1 ? recipeBgFilterOpts[0] : {}
    },
    // 
    async getRecipeUrl(fileName) {
      const imageFile = await this.$refs.htmlToImage.htmlToCanvas(true)
      imageFile.name = fileName
      const { url } = await upload(imageFile, progress => {})
      return url
    },
    // 
    sendTask() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          this.sendLoading = true
          this.getRecipeUrl(this.currentTask._id).then(url => {
            const { title, desc } = this.ruleForm
            const data = {
              recipe_template: this.templateId,
              content: this.tableContent,
              description: this.description,
              title,
              desc,
              url
            }
            upsetTask(this.currentTask._id, 2, data)
              .then(res => {
                if (res.code === 20000) {
                  this.sendLoading = false
                  this.$message.success('')
                  this.$router.back(-1)
                }
              })
              .catch(() => {
                this.sendLoading = false
              })
          })
        } else {
          return false
        }
      })
    },
    // 
    saveTask() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          const { title, desc } = this.ruleForm
          const data = {
            recipe_template: this.templateId,
            content: this.tableContent,
            description: this.description,
            title,
            desc
          }
          upsetTask(this.currentTask._id, 1, data).then(res => {
            if (res.code === 20000) {
              this.$message.success('')
            }
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>
<style scoped lang="scss">
@import '@/styles/healthManagement/addtask.scss';
@import '@/styles/recipes/recipes.scss';
.recipe__body__notes {
  white-space: pre-line;
}
.recipes-image {
  margin-left: 260px;
  width: 640px;
}
.user-info-label {
  font-weight: bold;
}
.edit-container {
  .edit-content {
    display: flex;
    .edit-section {
      min-height: 1000px;
    }
  }
}
</style>
